import React from 'react';
import { DistrictsMap } from '../DistrictsMap';


export const Central = () => {
    const central = "Центральный федеральный округ";
    const subjectId = 11;

    return (
        <>
        <div className="districts">
            <div className="districts-wrapper">
                <DistrictsMap 
                    subject={central}
                    subjectId={subjectId}
                />
            </div>
        </div>
        </>
    )
}