import React from 'react';
import { DistrictsMap } from '../DistrictsMap';


export const Southern = () => {
    const southern = "Южный федеральный округ";
    const subjectId = 3;

    return (
        <>
        <div className="districts">
            <div className="districts-wrapper">
                <DistrictsMap 
                    subject={southern} 
                    subjectId={subjectId}
                />
            </div>
        </div>
        </>
    )
}