import React from 'react';
import { DistrictsMap } from '../DistrictsMap';


export const NorthCaucasian = () => {
    const northCaucasian = "Северо-Кавказский федеральный округ";
    const subjectId = 8;

    return (
        <>
        <div className="districts">
            <div className="districts-wrapper">
                <DistrictsMap 
                    subject={northCaucasian}
                    subjectId={subjectId}
                />
            </div>
        </div>
        </>
    )
}