import React from 'react';
import { DistrictsMap } from '../DistrictsMap';


export const NorthWestern = () => {
    const northWestern = "Северо-западный федеральный округ";
    const subjectId = 49;

    return (
        <>
        <div className="districts">
            <div className="districts-wrapper">
                <DistrictsMap 
                    subject={northWestern} 
                    subjectId={subjectId}
                />
            </div>
        </div>
        </>
    )
}