import React from 'react';
import { DistrictsMap } from '../DistrictsMap';


export const Siberia = () => {
    const siberia = "Сибирский федеральный округ"
    const subjectId = 60;
    
    return (
        <>
        <div className="districts">
            <div className="districts-wrapper">
                <DistrictsMap 
                    subject={siberia} 
                    subjectId={subjectId}
                />
            </div>
        </div>
        </>
    )
}