import React from 'react';
import { DistrictsMap } from '../DistrictsMap';


export const FarEast = () => {
    const farEast = "Дальневосточный федеральный округ";
    const subjectId = 27;

    return (
        <>
        <div className="districts">
            <div className="districts-wrapper">
                <DistrictsMap 
                    subject={farEast} 
                    subjectId={subjectId}
                />
            </div>
        </div>
        </>
    )
}