import React from 'react';
import { DistrictsMap } from '../DistrictsMap';


export const Volga = () => {
    const volga = "Приволжский федеральный округ";
    const subjectId = 55;

    return (
        <>
        <div className="districts">
            <div className="districts-wrapper">
                <DistrictsMap 
                    subject={volga} 
                    subjectId={subjectId}
                />
            </div>
        </div>
        </>
    )
}