import React from 'react';
import { DistrictsMap } from '../DistrictsMap';

export const Ural = () => {
    const ural = "Уральский федеральный округ";
    const subjectId = 89;

    return (
        <>
        <div className="districts">
            <div className="districts-wrapper">
                <DistrictsMap 
                    subject={ural} 
                    subjectId={subjectId}
                />
            </div>
        </div>
        </>
    )
}